@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cards {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 350px 120px 80px;
  grid-template-areas: "image" "text" "stats";
  border-radius: 18px;
  background: #242526;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.9);
  font-family: roboto;
  text-align: center;
}
.cards-image {
  grid-area: image;
}
.cards-text {
  grid-area: text;
}
.cards-stats {
  grid-area: stats;
}
.cards-image {
  grid-area: image;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 100%;
}
.cards-text {
  grid-area: text;
}
.cards-text h2 {
  text-align: left;
  margin: 0 10px;
  font-size: 1.4rem;
}
.cards-text p {
  text-align: left;
  margin: 0 10px;
  color: white;
  font-size: 1.2rem;
  font-weight: 300;
}
.cards-stats {
  grid-area: stats;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background: grey;
}
.cards-stats .stat {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
  padding: 3px;
}
.cards-stats .value p {
  font-size: 1.4rem;
  color: white;
}
.cards:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.6);
}
.cards {
  transition: 0.5s ease;
  cursor: pointer;
  margin: 30px;
}

.MovieForm {
  padding-bottom: 20px;
  margin-bottom: 20px;
  text-align: center;
}
.MovieForm input {
  width: 50%;
  height: 50px;
  border: 1px solid #dcdcdc;
  padding: 3px 25px;
  color:#B0B3B8;
}

.Modal .bg {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #000;
  opacity: 0.7;
}
.Modal .popup {
  border-radius: 15px;
  position: fixed;
  width: 70%;
  height: 500px;
  left: 50%;
  top: 50%;
  background-color: #404040;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 20px;
}
.Modal .popup h2 {
  font-size: 25px;
}
.Modal .popup div {
  width: 100%;
  height: 450px;
  overflow: hidden;
}
.Modal .popup iframe {
  margin-bottom: 2rem;
  width: 100%;
  height: 450px;
}
.Modal .popup p {
  font-size: 25px;
  margin-top: 10px;
}
.Modal .popup span {
  position: absolute;
  right: 20px;
  top: 20px;
}
.Modal .popup span i {
  font-size: 35px;
}
