ul,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
table,
th,
td,
address,
p {
  margin: 0;
  padding: 0;
  color: #666;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 12px;
}
html,
body {
  width: 100%;
  height: 100%;
  line-height: 1.5;
}
ul,
